@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+Display:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&family=Noto+Serif+Display:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
.page {
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  background-color: #E8E0F5;
}
.App-header {
  background-color: #4e2a84;
  min-height: 7vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(8px + 2vmin);
  color: white;
  padding: 15px;
}
.moreinfo {
  color: white;
}
.logo {
  border-radius: 5px;
}
.Main {
  background-color: #E8E0F5;
  /* min-height: 100vh; */
  padding: 20px;
}
.inputform {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 30vh;
  justify-content: space-around;
}
.similarcounties {
  display: flex;
  flex-direction: row;
}
.plusminbutton {
  background-color: none;
  text-decoration: none;
  border: none;
}
input[type=text] {
  border: none;
  border-bottom: 3px solid #4e2a84;
  background-color: #E8E0F5;
  outline: none;
  font-family: 'Montserrat', sans-serif;
  font-size: large;
  text-align: center;
}
#simcount {
  width: 65px;
}
input[type=submit] {
  background-color: #4e2a84;
  border: none;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  color: white;
  border-radius: 5px;
  font-family: 'Montserrat', sans-serif;
}
.updownbutton{
  display: flex;
  flex-direction: column;
}
/* input[type=text]:focus {
  border: none;
} */
.submitbutton {
  width: 30px;
}
#inputbar {
  align-items: center;
  justify-content: center;
}
.descrip {
  text-align: center;
  padding: 20px;
}
.descrip h1 {
  color: #4e2a84;
  font-size: calc(10px + 2vmin);
}
.descrip h2 {
  font-size: calc(3px + 2vmin);
}
.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

